<template>
	<div class="px-5 mx-5">
		<CommonComment :api="() => getEventCommentApi(threadId)" :showAll="true" />
	</div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { useRoute } from "vue-router";
import { getEventCommentApi } from "@/api/common";
export default {
	setup(props) {
		const route = useRoute();
		const state = reactive({
			threadId: route.query.threadId,
		});
		return {
			...toRefs(state),
			getEventCommentApi,
		};
	},
};
</script>

